	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"ГЕРБАРИЙ","type":"lyrics"},"html":"<h1>ГЕРБАРИЙ</h1>\n<h2>э´л’ь</h2>\n<section>\n<p>Мы с тобой в холодных подземельях<br>\nцеловались в сахарный огонь!<br>\nВынесли нас хрупкие олени<br>\nна рогах, похожих на ладонь.</p>\n<p>Речка с разветвлёнными хвостами<br>\nзадевала путников, шипя.<br>\nРыбы в ней тонувшими свистками<br>\nпропускали время сквозь себя.</p>\n<p>Лез на лес зелёной тканью парус,<br>\nмышцами деревья напряглись — <br>\nи сползла трухою рыхлой старость,<br>\nоголяя меж стволами жизнь!</p>\n<p>Так сложились кости леса в судно.<br>\nГде‐то тут в оленях и рогах<br>\nмы с тобой вросли в тугие судьбы<br>\nтропами на выжженных руках.</p>\n<p>Мы плывём по Солнцу,<br>\nнаш корабль чует запах гари.<br>\nТам, где мы проснёмся,<br>\nтам и расцветёт гербарий.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/ru/lyrics/gerbarij/"}}

